export class CentroCosto {
    idCentroCosto: Number;
    tipo: String;
    descripcion: String;
    activo: boolean;
    constructor() {
        this.idCentroCosto = 0;
        this.tipo = '';
        this.descripcion = '';
        this.activo = true;
    }
}



